  right: -10%;
  transform: rotate(360deg);
}

.c-hamburger.is-open span {
  background-color: #fff;
  box-shadow: 1px 1px 5px 1px #fff;
  width: 40px;
  height: 5px;
  top: 25px;
  right: 0;
}
.c-hamburger.is-open .line-top {
  animation: line-top 0.6s linear forwards;
}
.c-hamburger.is-open .line-middle {
  box-shadow: 0 0 10px 6px #fff;
  animation: line-middle 0.6s linear forwards;
}
.c-hamburger.is-open .line-bottom {
  animation: line-bottom 0.6s linear forwards;
}
.c-hamburger.is-open ~ .c-overlay {
  will-change: transform;
  border-right: none;
  border-bottom: none;
  top: 22px;
  animation: overlay 0.6s linear forwards;
}

@keyframes overlay {
  70% {
    transform: scale(20);
  }
  100% {
    transform: scale(30);
  }
}
@keyframes line-top {
  0% {
    background-color: yellow;
  }
  40% {
    box-shadow: 1px 1px 4px 4px yellow;
    width: 100px;
    height: 10px;
    top: 50px;
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(135deg);
  }
}
@keyframes line-bottom {
  0% {
    background-color: yellow;
  }
  40% {
    box-shadow: 1px 1px 4px 4px yellow;
    width: 100px;
    height: 10px;
    top: 50px;
    transform: rotate(310deg);
  }
  100% {
    transform: rotate(405deg);
  }
}
@keyframes line-middle {
  0% {
    right: 10px;
  }
  100% {
    right: 110vw;
  }
}
