.c-light {
  position: absolute;
  opacity: 0.5;
  left: 0%;
  width: 100%;
  height: 100%;
  background: radial-gradient(at 50% 0, rgba(229, 255, 0, 0.8), transparent);
  z-index: 30;
  animation: over 15s ease forwards;
}

@keyframes over {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}
.c-linear-light {
  position: relative;
  width: 300px;
  height: 3px;
}
.c-linear-light::before, .c-linear-light::after {
  content: "";
  position: absolute;
}
.c-linear-light:before {
  display: block;
  width: 100%;
  height: 100%;
  bottom: -50px;
  transform-origin: 0 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.4) 80%, white);
  border-radius: 50%;
  animation: line 2s infinite;
}
.c-linear-light:after {
  width: 30px;
  height: 21px;
  bottom: -60px;
  transform-origin: 90% 50%;
  border-radius: 2px;
  background: rgba(210, 189, 255, 0.55);
  box-shadow: 0 0 10px 6px rgba(210, 189, 255, 0.4), -20px 0 15px 4px rgba(210, 189, 255, 0.3), -40px 0 15px 2px rgba(210, 189, 255, 0.2), -60px 0 10px 1px rgba(210, 189, 255, 0.1), -80px 0 10px 1px rgba(210, 189, 255, 0.05);
  filter: blur(8px);
  animation: light 2s infinite;
}

@keyframes line {
  0% {
    transform: scaleX(0);
    opacity: 0.5;
  }
  90% {
    transform: scaleX(1);
    opacity: 1;
  }
  92% {
    transform: scaleX(1);
    opacity: 1;
  }
  100% {
    transform: scaleX(1);
    opacity: 0;
  }
}
@keyframes light {
  0% {
    transform: scale(0.3, 0.8) translateZ(0);
    left: 0;
    box-shadow: 0 0 10px 6px rgba(255, 255, 0, 0.4), -20px 0 15px 4px rgba(255, 255, 0, 0.3), -40px 0 15px 2px rgba(255, 255, 0, 0.2), -60px 0 10px 1px rgba(255, 255, 0, 0.1), -80px 0 10px 1px rgba(255, 255, 0, 0.05);
    background: rgba(255, 255, 0, 0.5);
    opacity: 1;
  }
  22% {
    transform: scale(2, 2) translateZ(0);
    background: rgba(210, 189, 255, 0.55);
  }
  50% {
    transform: scale(1, 1) translateZ(0);
    box-shadow: 0 0 10px 6px rgba(210, 189, 255, 0.4), -20px 0 15px 4px rgba(210, 189, 255, 0.3), -40px 0 15px 2px rgba(210, 189, 255, 0.2), -60px 0 10px 1px rgba(210, 189, 255, 0.1), -80px 0 10px 1px rgba(210, 189, 255, 0.05);
    opacity: 0.5;
  }
  100% {
    transform: scale(1, 1) translateZ(0);
    left: 105%;
    opacity: 0;
  }
}
