.p-loading-panel, .p-loading-panel--right, .p-loading-panel--left {
  position: fixed;
  width: 60%;
  height: 100vh;
  top: 0;
  background: -webkit-radial-gradient(36% 65%, circle, rgba(249, 249, 250, 0) 0%, rgba(189, 255, 254, 0) 0%, #08314e 0%, #081a38 80%, #04102e 90%, #02080d 100%);
}
.p-loading-panel__wrapper {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  overflow: hidden;
  opacity: 0.8;
  z-index: 1500;
}
.p-loading-panel--left {
  left: 0;
  border-right: 4px solid #670c67;
  box-shadow: inset -40px 0 50px rgba(255, 255, 0, 0.3), inset -90px 0 120px rgba(255, 255, 255, 0.3);
  will-change: animation;
  animation: loading-left 4s ease-out forwards;
}
.p-loading-panel--right {
  right: 0;
  border-left: 4px solid #670c67;
  box-shadow: inset 40px 0 50px rgba(255, 255, 0, 0.3), inset 90px 0 120px rgba(255, 255, 255, 0.3);
  will-change: animation;
  animation: loading-right 4s ease-out forwards;
}
.p-loading-panel--left::before, .p-loading-panel--right::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  opacity: 0.11;
  background: url("../img/top/loading.jpeg") repeat center center;
}
.p-loading-panel--left::before {
  right: 0;
  animation: loading-left-before 4s ease-out forwards;
}
.p-loading-panel--right::before {
  left: 0;
  animation: loading-right-before 4s ease-out forwards;
}

@keyframes loading-left {
  0% {
    transform: translate(0, 0) rotate(0deg);
    border-bottom-right-radius: 20%;
    top: 0;
  }
  10% {
    border-bottom-right-radius: 100%;
  }
  100% {
    transform: translate(-50vw, -50vh) rotate(20deg);
    border-bottom-right-radius: 100%;
    top: -20%;
  }
}
@keyframes loading-right {
  0% {
    transform: translate(0, 0) rotate(0deg);
    border-bottom-left-radius: 20%;
    top: 0;
  }
  10% {
    border-bottom-left-radius: 100%;
  }
  100% {
    transform: translate(50vw, -50vh) rotate(-20deg);
    border-bottom-left-radius: 100%;
    top: -20%;
  }
}
@keyframes loading-left-before {
  0% {
    border-bottom-right-radius: 20%;
  }
  10% {
    border-bottom-right-radius: 90%;
  }
  100% {
    border-bottom-right-radius: 100%;
  }
}
@keyframes loading-right-before {
  0% {
    border-bottom-left-radius: 20%;
  }
  10% {
    border-bottom-left-radius: 90%;
  }
  100% {
    border-bottom-left-radius: 100%;
  }
}
#marker {
  position: absolute;
  top: 7px;
  left: calc(100% - 30px);
  transition: 0.5s;
  z-index: 1;
}
#marker::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
