:root {
  --planet-anim-duration: 0;
  --planet-anim-delay: 0;
  --planet-color: 0;
  --planet-size: 40px;
}

.p-orbit {
  position: absolute;
  width: 100%;
  height: 100%;
}
.p-orbit__container {
  position: absolute;
  width: 1050px;
  height: 400px;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
}
.p-orbit:nth-child(1) {
  transform: rotate(-13deg) scale(0.82);
  animation: orbit1 8s infinite alternate;
}
@keyframes orbit1 {
  0% {
    transform: rotate(-9deg) scale(0.85);
  }
  100% {
    transform: rotate(26deg) scale(0.98);
  }
}
.p-orbit:nth-child(2) {
  transform: rotate(-7deg) scale(0.98);
  animation: orbit2 8s infinite alternate;
}
@keyframes orbit2 {
  0% {
    transform: rotate(-15deg) scale(0.85);
  }
  100% {
    transform: rotate(29deg) scale(0.89);
  }
}
.p-orbit:nth-child(3) {
  transform: rotate(-13deg) scale(0.83);
  animation: orbit3 8s infinite alternate;
}
@keyframes orbit3 {
  0% {
    transform: rotate(-10deg) scale(0.85);
  }
  100% {
    transform: rotate(31deg) scale(0.84);
  }
}
.p-orbit:nth-child(4) {
  transform: rotate(-7deg) scale(0.81);
  animation: orbit4 8s infinite alternate;
}
@keyframes orbit4 {
  0% {
    transform: rotate(-9deg) scale(0.8);
  }
  100% {
    transform: rotate(24deg) scale(1);
  }
}
.p-orbit__path {
  stroke: #979797;
  stroke-width: 1;
  fill: none;
  opacity: 0.45;
}
.p-orbit .planet {
  width: var(--planet-size);
  height: var(--planet-size);
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--planet-color);
  border-radius: 50%;
  offset-path: path("M25,200a487,180 0 1,0 974,0a487,180 0 1,0 -974,0");
  animation: move linear infinite, breathe linear infinite;
  animation-duration: var(--planet-anim-duration), 2s;
  animation-delay: var(--planet-anim-delay);
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}
@keyframes breathe {
  0% {
    box-shadow: 0 0 6px var(--planet-color);
  }
  50% {
    box-shadow: 0 0 25px var(--planet-color);
  }
  100% {
    box-shadow: 0 0 6px var(--planet-color);
  }
}
.p-particle--bang {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.p-page-top {
  position: fixed;
  bottom: 51%;
  left: 20px;
}
.p-page-top span {
  color: #000;
  font-size: 1rem;
  font-weight: 400;
  position: absolute;
  text-align: right;
  top: -30%;
  right: 0;
}
.p-page-top figure {
  width: 50px;
  animation: rabbit 2s infinite;
}
.p-page-top figure img {
  width: 100%;
}

@keyframes rabbit {
  0% {
    transform: translateY(10%);
  }
  90% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0);
  }
}
.p-random-txt, .p-random-txt--color {
  margin: 1em auto;
  position: absolute;
  transform-style: preserve-3d;
  perspective: 400px;
  text-align: center;
  max-width: 700px;
  bottom: 10%;
  right: 5%;
}
.p-random-txt .letter, .p-random-txt .letter--color, .p-random-txt--color .letter, .p-random-txt--color .letter--color,
.p-random-txt .space,
.p-random-txt--color .space {
  position: relative;
  display: inline-block;
  font-size: 1.8rem;
  font-family: "Titillium Web", sans-serif;
  font-weight: bold;
  transform-style: preserve-3d;
}
.p-random-txt .space, .p-random-txt--color .space {
  width: 0.5em;
}

.p-random-txt--color {
  width: 200px;
  height: 100px;
  top: 46%;
  left: 45.5%;
  bottom: inherit;
  right: inherit;
  animation: fade 1s;
}

