.p-saturn {
  position: absolute;
  top: 0;
  right: 1%;
  opacity: 0.7;
  animation: saturn 3s infinite ease-in-out 0.8s alternate;
  transition: 1.5s ease-in-out;
}
.p-saturn img {
  width: 150px;
}

@keyframes saturn {
  0% {
    transform: translate(0, 0) rotate(40deg);
  }
  50% {
    transform: translate(0, -3px) rotate(30deg);
  }
  100% {
    transform: translate(0, 0) rotate(20deg);
  }
}
.p-scroll-anim {
  opacity: 0;
}
.p-scroll-anim::before {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: url("../img/common/rabbit.png");
  background-color: #ffff65;
  filter: blur(5px);
  z-index: 10;
}
.p-scroll-anim.show {
  opacity: 1;
}
.p-scroll-anim.show::before {
  animation: down 2s both;
}
.p-scroll-anim.show::before:nth-child(1) {
  animation-delay: 500s;
}
.p-scroll-anim.show::before:nth-child(2) {
  animation-delay: 1000s;
}
.p-scroll-anim.show::before:nth-child(3) {
  animation-delay: 1500s;
}

@keyframes down {
  0% {
    opacity: 1;
    transform: translate(0, -500px);
  }
  50% {
    opacity: 1;
    transform: translate(0, 0);
  }
  100% {
    opacity: 1;
    transform: translate(300px, 0);
  }
}
