@keyframes html {
  0% {
    transform: translate(0);
  }
  10% {
    transform: scale(2) rotateZ(20deg);
  }
  20% {
    transform: scale(2) rotateZ(-80deg);
  }
  30% {
    transform: scale(1) rotateZ(360deg);
  }
  35% {
    transform: scale(1) rotateZ(-360deg);
  }
  100% {
    transform: scale(0.5) rotateZ(-720deg);
  }
}
@keyframes css {
  0% {
    transform: scale(1) rotateZ(0deg);
  }
  10% {
    transform: scale(2) rotateZ(-360deg);
  }
  20% {
    transform: scale(2) rotateZ(0deg);
  }
  30% {
    transform: scale(1) rotateZ(360deg);
  }
  100% {
    transform: scale(1) rotateZ(0deg);
  }
}
@keyframes php {
  0% {
    transform: scale(1) rotateZ(0deg);
  }
  70% {
    transform: scale(2) rotateZ(360deg);
  }
  80% {
    transform: scale(2) rotateZ(0deg);
  }
  82% {
    transform: scale(1) rotateZ(360deg);
  }
  84% {
    transform: scale(1) rotateZ(-360deg);
  }
  100% {
    transform: scale(1) rotateZ(0deg);
  }
}
@keyframes js {
  0% {
    transform: translate(0);
  }
  10% {
    transform: scale(2) rotateZ(20deg);
  }
  12% {
    transform: scale(2) rotateZ(-80deg);
  }
  20% {
    transform: scale(1) rotateZ(360deg);
  }
  25% {
    transform: scale(1) rotateZ(-360deg);
  }
  100% {
    transform: scale(1) rotateZ(-720deg);
  }
}
@keyframes react {
  0% {
    transform: rotateZ(0);
  }
  80% {
    transform: rotateZ(360deg);
  }
  85% {
    transform: rotateZ(-360deg);
  }
  100% {
    transform: rotateZ(720deg);
  }
}
@keyframes gulp {
  0% {
    transform: translateY(-40px) rotateZ(0deg);
  }
  50% {
    transform: translateY(40px) rotateZ(360deg);
  }
  100% {
    transform: translateY(-40px) rotateZ(0deg);
  }
}
@keyframes git {
  0% {
    transform: translateY(-40px) rotateZ(0deg);
  }
  50% {
    transform: translateY(40px) rotateZ(-360deg);
  }
  100% {
    transform: translateY(-40px) rotateZ(0deg);
  }
}
